.main-wrapper{
    padding: 100px 0;
}
.side-menu{
    padding: 20px;
    background: #f7f7f7;
    top: 20px;
    max-height: 80vh;
    overflow-y: scroll;
    position: sticky;
    ul{
        list-style: none;
        padding-left: 10px;
        li{
            text-transform: uppercase;
            &:not(:last-child){
                padding-bottom: 10px;
            }
            a{
                color: rgb(49, 49, 49);
            }
        }
    }
}

.content-div{
    h4{
        margin-top: 30px;
    }
    .contents{
        margin: 20px 0;
        .bootstrap-switch{
            margin-right: 20px;
        }
    }
    &.buttons{
        button{
            margin-right: 7px;
        }
    }
    .custom-control{
        margin-bottom: 10px;
    }
}